import * as React from 'react'
import styled from '../styles/styled-components'

const StyledContainer = styled.section`
  padding: ${p => p.theme.dimensions.containerPadding * 2}rem ${p => p.theme.dimensions.containerPadding}rem;

  h2 {
    letter-spacing: 1.22px;
    max-width: 800px;
  }

  a {
    text-decoration: underline;
    font-weight: 700;
  }

  p {
    font-size: 1.25rem;
    max-width: 800px;
  }
`

interface DividerProps {
  className?: string
  header: string
  text?: React.ReactNode
}

export const Divider: React.SFC<DividerProps> = ({ className, header, text }) => (
  <StyledContainer className={className}>
    <h2>{header}</h2>
    {!!text && <p>{text}</p>}
  </StyledContainer>
)



